import React from 'react'
import Context from '../../Barbershop/Components/Context'
import './DradonGame.css'
import { HashRouter, Route, Link } from 'react-router-dom'
import CasHeader from '../header/casHeader'

class DradonGame extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }

    componentDidMount() {

        function refrechBalanceDisplay() {
            // обновить баланс
            document.querySelector('.yourBalance').innerText = localStorage.getItem('balance')
        }

        let bombCountInput = document.getElementById('bombCount')
        let startGameBtn = document.getElementById('startGame')
        let rowIndex = 9
        let dragonDangenHeader = document.querySelector('.dragonDangenHeader')
        let bombCount;

        bombCountInput.onchange = () => {
            let bombCount = bombCountInput.value
            console.log(bombCount)
        }

        function clearField() {
            document.querySelectorAll('.field').forEach(item => item.style.background = '')
        }

        startGameBtn.onclick = () => {
            clearField()
            toggleBtnDisable(false)
            doStep()
            startGame()
        }

        function randomNumber(min, max) {
            return Math.floor(Math.random() * (max - min + 1)) + min
        }

        function createGame() {
            let game = document.createElement('table')
            game.className = 'dragonDangenGame'

            for (let i = 0; i < 10; i++) {

                let row = document.createElement('tr')
                row.className = 'dfjcsa row row' + i;

                for (let x = 0; x < 10; x++) {
                    let btn = document.createElement('button')
                    btn.className = 'field '
                    row.appendChild(btn)
                    btn.onclick = () => console.log(0)
                }

                game.appendChild(row)

            }
            
            dragonDangenHeader.after(game)
        }

        createGame()

        function startGame() {

            let bal = localStorage.getItem('balance')
            // bal = 

            rowIndex = 9;
            doStep()
            function tryPutBomb(item) {
                if (randomNumber(1, 2) == 2) {
                    return true;
                }
            }

            document.querySelectorAll('.row').forEach(item => {
                let maxBombsCount = bombCountInput.value;

                item.querySelectorAll('.field').forEach(item => {
                    if (maxBombsCount > 0) {
                        if (tryPutBomb(item) == true) {
                            item.style.background = 'black'
                            maxBombsCount--;
                        }
                    }
                })
            }
            )
        }

        document.querySelectorAll('.field').forEach(item => {
            item.onclick = () => {
                doStep()
                if (item.style.background == 'black') {
                    console.log('lose')
                    toggleBtnDisable(true)

                    // let bal = localStorage.getItem('balance')
                    // note

                } else {
                    console.log('dont lose')
                }

                refrechBalanceDisplay()
            }
        })

        function toggleBtnDisable(value) {
            if (value == true) {
                document.querySelectorAll('.field').forEach(item => {
                    item.disabled = 'true'
                })
            }
            if (value == false) {
                document.querySelectorAll('.field').forEach(item => {
                    item.removeAttribute("disabled")
                })
            }
        }

        function rowStep(index) {

            toggleBtnDisable(true)

            for (let i = 0; i < 10; i++) {
                if (i == index) {
                    let line = '.row' + index

                    console.log(
                        document.querySelector(line)
                            .querySelectorAll('.field')
                            .forEach(item => {
                                item.removeAttribute("disabled")
                            })
                    )

                    // document.getElementById(line).querySelectorAll('.field').forEach(item => {
                    //     // item.disabled = 'true'
                    //     console.log(index)
                    // })
                }
            }
        }

        function doStep() {
            rowStep(rowIndex)
            rowIndex--;
        }

        

    }

    render() {


        return (
            <Context.Consumer>
                {(Context) => {
                    return (
                        <div>

                            <div class="dragonDangen bet ">

                                <CasHeader />


                                <nav className='dragonDangenHeader df ai_center'>
                                    <input type="number" id="bombCount" min='1' max='10' placeholder='1' className='dragonGameBet' />
                                    <button id="testBtn" className="dragonGameBet" >test</button>
                                    <button id='startGame' className='dragonGameBet'>start game</button>
                                    <button className='black ' onClick={() => {
                                        let bal = localStorage.getItem('balance')
                                        localStorage.setItem('balance', Number(bal) + Number(100))
                                        console.log(localStorage.getItem('balance'))
                                    }}>a</button>
                                </nav>

                            </div>

                        </div>
                    )

                }}

            </Context.Consumer >
        )
    }

}

export default DradonGame;
