import React from 'react'
import { HashRouter, Route, Link } from 'react-router-dom'
import '../header/casHeader.css'

class CasHeader extends React.Component {
  constructor(props) {
    super(props)
  }


  render() {
    return (
      <div>
        <nav className='df_jcsa casHeader'>
          <Link className="" to="/roulette">Рулетка</Link>
          <Link to='/highlow'> Highlow </Link>
          <Link to='/dradonGame'>Dragon</Link>
        </nav>

        <div className='df yourBalance'>{localStorage.getItem('balance')}</div>
      </div>
    )
  }

}
export default CasHeader;