import React from 'react'
import Context from '../../Barbershop/Components/Context'
import './style.css'
import '../../Barbershop/App.css'
import CasHeader from '../header/casHeader'

class Highlow extends React.Component {
    // constructor(props) {
    //     super(props)
    //     this.state = {
    //     }
    // }

    componentDidMount() {


        // highlow
        setTimeout(() => {
            let bet, newBalance, pastNumber;
            let firstBet = true;
            let betX = document.getElementById('betX');
            let btnStartPlay = document.getElementById('btnStartPlay')
            let btnStopPlay = document.getElementById('btnStopPlay')
            let btnHigh = document.getElementById('btnHigh')
            let btnLow = document.getElementById('btnLow')
            let balanceDisplay = document.getElementById('balanceDisplay')
            let betSizeInput = document.getElementById('betSizeInput')
            let numbersColumn = document.getElementById('numbersColumn')
            let currentNumber = document.getElementById('currentNumber').innerText = randomNumber(1, 100)
            let betMultiplyArr = [1, 1.25, 1.5, 2, 3.5, 7, 12, 15, 17, 50]
            let betMultiplyIndex = 0;
            document.getElementById('betX').innerText = betMultiplyArr[betMultiplyIndex]


            function refrechBalanceDisplay() {
                // обновить баланс
                document.querySelector('.yourBalance').innerText = localStorage.getItem('balance')
            }

            function checkWin() {
                if (betMultiplyIndex == 9) {
                    console.log('total win')
                    document.querySelector('.inform').innerText = 'Победа'

                    setTimeout(() => {
                        document.querySelector('.inform').innerText = 'Сделайте ставку'
                    }, 2000);

                    btnStartPlay.removeAttribute("disabled");
                    btnHigh.setAttribute("disabled", "true");
                    btnLow.setAttribute("disabled", "true");
                }

            }

            document.querySelectorAll('.after').forEach(item => {
                item.style.display = 'none'
            })

            btnStartPlay.setAttribute("disabled", "true");
            // btnStart.onclick = () => localStorage.setItem('balance', 1000)
            balanceDisplay.innerText = localStorage.getItem('balance')
            document.getElementById('currentNumber').innerText = randomNumber(1, 100)

            betSizeInput.addEventListener('input', checkBetInput);

            function checkBetInput(e) {
                if (betSizeInput.value == '' ||
                    betSizeInput.value < 1 ||
                    localStorage.getItem('balance') - betSizeInput.value < 0
                ) {
                    btnStartPlay.setAttribute("disabled", "true");
                } else {
                    btnStartPlay.removeAttribute("disabled")
                }
            }

            function displayNewNumber(pastNumber) {
                let div = document.createElement('div')
                div.className = 'number df'
                div.innerHTML = `
                    <div class="squad">${pastNumber}</div>
                    <div class="squad">число</div>
        `
                numbersColumn.prepend(div)
            }

            function randomNumber(min, max) {
                return Math.floor(Math.random() * (max - min + 1)) + min
            }

            document.querySelectorAll('.betSize').forEach(item => {
                item.onclick = () => {
                    betSizeInput.value = bet = item.innerText
                    btnStartPlay.removeAttribute("disabled")

                    if (localStorage.getItem('balance') < 10) {
                        btnStartPlay.setAttribute("disabled", "true");
                    }
                }
            })

            btnStartPlay.onclick = () => {

                console.log(1)
                //сбросить кэф
                betMultiplyIndex = 0;

                // показать больше/меньше/вывод
                document.querySelectorAll('.after').forEach(item => {
                    item.style.display = 'flex'
                })

                // сделать больше/меньше активными
                btnHigh.removeAttribute("disabled");
                btnLow.removeAttribute("disabled");

                // отнять ставку
                let bal = localStorage.getItem('balance')
                localStorage.setItem('balance', Number(bal) - Number(betSizeInput.value))
                // balanceDisplay.innerText = localStorage.getItem('balance')

                refrechBalanceDisplay()
                btnStartPlay.setAttribute("disabled", "true");
                btnStopPlay.setAttribute("disabled", "true");
            }

            btnStopPlay.onclick = () => {
                // вывести
                btnStartPlay.removeAttribute("disabled");
                btnStopPlay.setAttribute("disabled", "true");
                btnHigh.setAttribute("disabled", "true");
                btnLow.setAttribute("disabled", "true");

                let currentBalance = Number(localStorage.getItem('balance'))
                newBalance = Number(currentBalance) + Number(betSizeInput.value) * Number(betMultiplyArr[betMultiplyIndex])
                localStorage.setItem('balance', newBalance)
                balanceDisplay.innerText = localStorage.getItem('balance')

                refrechBalanceDisplay()
            }

            btnHigh.onclick = () => {

                if (firstBet == true) {
                    console.log('true')
                    firstBet = false;
                    pastNumber = document.getElementById('currentNumber').innerHTML
                } else {
                    pastNumber = currentNumber
                }

                currentNumber = randomNumber(1, 100) // по хорошему get-запрос

                displayNewNumber(currentNumber)
                checkWin()

                if (pastNumber >= currentNumber) {
                    document.querySelector('.inform').innerText = pastNumber + '>=' + currentNumber + ' win'
                    // console.log(pastNumber + '>=' + currentNumber + ' win')

                    betMultiplyIndex++;
                    betX.innerText = betMultiplyArr[betMultiplyIndex]
                    btnStopPlay.removeAttribute("disabled")
                } else {
                    document.querySelector('.inform').innerText = pastNumber + '<' + currentNumber + ' lose'
                    // console.log(pastNumber + '<' + currentNumber + ' lose')
                    document.querySelectorAll('.after').forEach(item => {
                        item.style.display = 'none'
                    })
                    setTimeout(() => {
                        document.querySelector('.inform').innerText = 'Сделайте ставку'
                    }, 2000);

                    btnHigh.setAttribute("disabled", "true");
                    btnLow.setAttribute("disabled", "true");
                    btnStartPlay.removeAttribute("disabled");
                    btnStopPlay.setAttribute("disabled", "true");
                    // balanceDisplay.innerText = localStorage.getItem('balance')
                    refrechBalanceDisplay()
                }
            }

            btnLow.onclick = () => {

                if (firstBet == true) {
                    console.log('true')
                    firstBet = false;
                    pastNumber = document.getElementById('currentNumber').innerHTML
                } else {
                    pastNumber = currentNumber
                }

                currentNumber = randomNumber(1, 100)
                displayNewNumber(currentNumber)
                checkWin()

                if (pastNumber <= currentNumber) {
                    document.querySelector('.inform').innerText = pastNumber + '>=' + currentNumber + ' win'
                    // console.log(pastNumber + '>=' + currentNumber + ' win')

                    betMultiplyIndex++;
                    betX.innerText = betMultiplyArr[betMultiplyIndex]
                    btnStopPlay.removeAttribute("disabled")
                } else {
                    document.querySelector('.inform').innerText = pastNumber + '<' + currentNumber + ' lose'
                    document.querySelectorAll('.after').forEach(item => {
                        item.style.display = 'none'
                    })
                    setTimeout(() => {
                        document.querySelector('.inform').innerText = 'Сделайте ставку'
                    }, 2000);

                    // console.log(pastNumber + '<' + currentNumber + ' lose')

                    btnHigh.setAttribute("disabled", "true");
                    btnLow.setAttribute("disabled", "true");
                    btnStartPlay.removeAttribute("disabled");
                    btnStopPlay.setAttribute("disabled", "true");
                    // balanceDisplay.innerText = localStorage.getItem('balance')
                    refrechBalanceDisplay()

                }
            }

        }, 1);

    }

    render() {
        return (
            <Context.Consumer>
                {(Context) => {
                    return (
                        <div className='highlowWrapper'>

                            {/* <nav>
                                <div className='df yourBalance'>{localStorage.getItem('balance')}</div>
                            </nav> */}

<CasHeader />

                            <div className="highlow df-col">
                                <div>
                                    <div className="section df border one">
                                        {/* <!-- <button id="btnStart" className="squad">refresh</button> --> */}

                                        <div className='df_jcsa'>
                                            <div className="squad">Баланс : </div>
                                            <div id="balanceDisplay" className="squad"> {} </div>
                                            <button className='squad' onClick={
                                                () => {
                                                    let a = Number(localStorage.getItem('balance'))
                                                    a = a.toFixed(2)
                                                    localStorage.setItem('balance', 1000)
                                                    document.getElementById('balanceDisplay').innerText = a
                                                }
                                            }> get bonus </button>
                                        </div>

                                        <div className='inform squad '>Сделайте ставку</div>
                                    </div>

                                    <div className="section df">
                                        <div className="bets">
                                            <input type="number" className="betValue" placeholder="bet value" id='betSizeInput' />
                                            <div className="df">
                                                <button className="betSize squad">10</button>
                                                <button className="betSize squad">20</button>
                                                <button className="betSize squad">30</button>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="section df">
                                        <button id='btnStartPlay' className='squad'>Начать</button>
                                        <div className="after" id='hide'>
                                            <button id="btnStopPlay" className='squad '>Вывести</button>
                                        </div>
                                        <div className="squad">
                                            <div>x</div>
                                            <div id='betX'></div>
                                        </div>
                                    </div>

                                    <div className="section after df">
                                        <button className="squad" id='btnHigh'>Больше</button>
                                        <button className="squad" id='btnLow'>Меньше</button>
                                        {/* <div className="squad border" id="gameSum">0</div> */}
                                    </div>
                                </div>

                                <div id="numbersColumn">
                                    <div className="number df">
                                        <div className="squad" id='currentNumber'>10</div>
                                        <div className="squad">число</div>
                                    </div>
                                </div>

                            </div>
                        </div>

                    )

                }
                }

            </Context.Consumer >
        )
    }

}

export default Highlow;
